
:root{
    --x:45deg;
}
.signupBtn{
    position: relative;
    width: 150px;
    height: 55px;
    display: inline-block;
    border-radius: 5px;
}
.signupBtn i{
    position: absolute;
    inset: -2px;
    display: block;
    border-radius:5px ;

}
.signupBtn i,
.signupBtn i:nth-child(2){
    background: linear-gradient(var(--x),#d15daa,#330e2d,#330e2d,#dc52dc);
}
.signupBtn i:nth-child(2){
    filter: blur(4px);

}
.signupBtn span{
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 2px;
    border: 1px solid #040a29;
    border-radius: 3px;
    background:#330e2d;
    overflow: hidden;
}

.signupBtn span::before{

    content: '';
    position: absolute;
    top: 0;
    left: -50%;
    width: 100%;
    height: 100%;
    background: rgba(255,255,255, 0.075);
    transform: skew(25deg);

}

.rippleBtn{
    position: relative;
    display: inline-block;
    padding: 12px 36px;
    margin: 10px 0;
    color: white;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 18px;
    letter-spacing: 2px;
    border-radius: 40px;
    background: linear-gradient(90deg,#755bea,#ff72c0);

}

