   .card-footer .sendIcon{
    
    color: #043b07;
    font-size: 25px;
    border-radius: 50%;
    background: #ebf2ee;
    padding: 4px;
    transition: 0.2s ease-in;
   
   }
   .card-footer .sendIcon:hover{
    background: #c4e8d4;
    color: #043b07;
    
   }

   .chat-heading{
    color: black;
    font-weight: bold;
    font-family: 'Lato', sans-serif;
    text-transform: capitalize;
    font-size: 20px;
   }
   .back-chat{
    transition: 0.2s ease-in;
    
   }
   .back-chat:hover{
    color: darkblue;
    
   }
