
.emptyState {
    width: 750px;
    margin: 40px auto;
    background: #ffffff;
    box-shadow: 1px 2px 10px #e1e3ec;
    border-radius: 4px;
  
        img {
          width: 170px;
        }
      }
      .emptyStateContent{
        padding: 48px;
        display: flex;
        align-items: center;
        flex-direction: column;
      }
      .emptyStateIcon{
        width: 200px;
        height: 200px;
        display: flex;
        align-items: center;
        border-radius: 200px;
        justify-content: center;
        background-color: #f7fafc;
        box-shadow: 0px 2px 1px #e1e3ec;
      }
  
      .emptyStateMessage {
        color: #024467;
        font-size: 1.5rem;
        font-weight: 500;
        margin-top: 0.85rem;
      }
  
      .emptyStateHelp {
        color: #a2a5b9;
        font-size: 0.875rem;
      }
    
  
  
  .credit {
    color: #A2A5B9;
    font-size: .75rem;
    text-align: center;
    
    a {
      color: #444;
    }
  }